@-webkit-keyframes ripple-effect {
  0% {
    -webkit-transform: translate3d(0%, 0%, 0) scale3d(0, 0, 1);
            transform: translate3d(0%, 0%, 0) scale3d(0, 0, 1);
    opacity: 0; }
  50% {
    -webkit-transform: translate3d(0%, 0%, 0) scale3d(2, 2, 1);
            transform: translate3d(0%, 0%, 0) scale3d(2, 2, 1);
    opacity: 0.85; }
  100% {
    -webkit-transform: translate3d(0%, 0%, 0) scale3d(4.5, 4.5, 1);
            transform: translate3d(0%, 0%, 0) scale3d(4.5, 4.5, 1);
    opacity: 0; } }

@keyframes ripple-effect {
  0% {
    -webkit-transform: translate3d(0%, 0%, 0) scale3d(0, 0, 1);
            transform: translate3d(0%, 0%, 0) scale3d(0, 0, 1);
    opacity: 0; }
  50% {
    -webkit-transform: translate3d(0%, 0%, 0) scale3d(2, 2, 1);
            transform: translate3d(0%, 0%, 0) scale3d(2, 2, 1);
    opacity: 0.85; }
  100% {
    -webkit-transform: translate3d(0%, 0%, 0) scale3d(4.5, 4.5, 1);
            transform: translate3d(0%, 0%, 0) scale3d(4.5, 4.5, 1);
    opacity: 0; } }

.c-ripple {
  position: relative; }

.c-ripple__ripple {
  position: absolute;
  top: -10px;
  left: -10px;
  border: 1px solid white;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  opacity: 0; }

.c-ripple--will-change > .c-ripple__ripple {
  will-change: transform, opacity; }

.c-ripple--active > .c-ripple__ripple {
  -webkit-animation: ripple-effect 0.45s linear 0s 1 normal both;
          animation: ripple-effect 0.45s linear 0s 1 normal both; }



