.c-group-indicator {
  display: inline-block;
  position: relative;
  width: 18px;
  height: 18px;
  border-radius: 18px;
  border: 1px solid transparent;
  border-color: inherit;
  outline: none;
  background: none; }

.c-group-indicator--large {
  width: 28px;
  height: 28px;
  border-radius: 28px;
  border-width: 1px; }

.c-group-indicator--focused {
  border-width: 2px; }

.c-group-indicator::before {
  content: '';
  position: absolute;
  top: -1px;
  right: -1px;
  bottom: -1px;
  left: -1px;
  z-index: 2;
  border-radius: 18px;
  border: 1px solid transparent;
  border-top-color: #fff;
  opacity: 0.4; }

.c-group-indicator--focused::before {
  opacity: 1;
  top: -2px;
  right: -2px;
  bottom: -2px;
  left: -2px;
  border-width: 2px; }

.c-technology-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  position: absolute;
  left: 50%;
  top: 50%;
  width: 50%;
  height: 1px;
  -webkit-transform-origin: 0;
      -ms-transform-origin: 0;
          transform-origin: 0;
  pointer-events: none; }

.c-technology-item__item {
  position: absolute;
  top: -9px;
  right: -9px;
  z-index: 100;
  pointer-events: all;
  cursor: pointer; }



